:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	display: inline-block;
}

.root *, .root *:before, .root *:after {
	-webkit-box-sizing: inherit;
	        box-sizing: inherit;
}

.inactiveDate {
	background-color: #bbb;
	background-color: var(--disabled-color);
}

.selectedDate {
	background-color: #34495e;
	background-color: var(--selected-background);
	color: #fff;
	color: var(--selected-color);
}

.todayDate {}

.date {
	cursor: pointer;
	padding: 10px 20px;
	text-align: center;
}

.date:hover {
	background-color: #bbb;
	background-color: var(--disabled-color); /* TODO: better hover color variable? */
}

/* Date grid */

.dateGrid {}

.weekday {
	padding: 10px 20px;
	text-align: center;
}

.weekday {
	color: #bbb;
	color: var(--disabled-color);
	font-weight: normal;
	text-transform: uppercase;
}

/* Date picker  */

.datePicker { }

.topMatter { }

.monthTrigger { }

.yearTrigger { }

.controls { }

.previous { }

.next { }

.monthGrid { }

.monthFields { }

.yearGrid { }

.yearFields { }

.monthRadio { }

.yearRadio { }

.monthRadioChecked { }

.yearRadioChecked { }

.monthRadioInput { }

.monthRadioLabel { }

.yearRadioInput { }

.yearRadioLabel { }

.monthTriggerActive { }

.yearTriggerActive { }



